import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
  padding-top : 20px;
`;

let 제목 = styled.h4`
  font-size : 30px;
  color : ${ props => props.색상 }
`;


/* Lifecycle Hook */
// 예전버전 형태
/* class Detail2 extends React.Component {
  componentDidMount(){
    //Detail2 컴포넌트가 Mount 되고나서 실행할 코드
  }
  componentWillUnmount(){
    //Detail2 컴포넌트가 Unmount 되기전에 실행할 코드
  }
} */


function Detail(props){

  let [alert, alertState] = useState(0);
  let [data, dataState] = useState('');

  let { id } = useParams(); // App.js에 <Route path='/detail/:id'> /:id에 들어가는 파라미터
  let history = useHistory();

  // 상품을 정렬을 하면 상세페이지의 데이터와 매칭되지 않는 경우
  // data.js 오브젝트 안에 id라는 고유번호를 사용해보자
  // 보통 이런경우 ajax를 사용하게됨 이건 뒤에서 배울려나...?
  // 일단 아래 방법으로 해보자
  let choice = props.shoes.find((a)=>{
      return(
          a.id == id
      )
  });

  /* useEffet Hook */
  // 컴포넌트가 mount 되었을때(보일때)
  // 컴포넌트가 업데이트 될때 코드가 실행됨
  useEffect(()=>{
    let timer = setTimeout(()=>{
      alertState(1);
    },2000)

    // return 함수는 컴포넌트가 unmount 되었을때(사라질때) 코드 실행
    return ()=>{ clearTimeout(timer) }
  
  },[alert]); // 대괄호안에 있는 state가 변경될때만 실행하도록 조건 걸어줌
              // [] 빈 대괄호 실행결과는 <Detail> 등장시 한번 실행하고 끝남
  
  return(
      <div className="container">
        <박스> {/* 왜 영어로 쓰면 css 안먹는거지??? */}
          <제목 색상={'red'}>Detail</제목>
          <제목 className='제목'>상세페이지</제목>
        </박스>
        {data}
        <input onChange={(e)=>{ dataState(e.target.value) }}/>
        {
          alert == 0 ? <Alert></Alert> : null
        }
        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{choice.title}</h4> {/* 정렬시 state 변경 문제가 있으므로 이렇게 사용 */}
            <p>{props.shoes[id].content}</p> {/* 예를들어 가격으로 정렬을 했을때 shoes라는 state가 변경되서 /detail/0 접속시 Red Knit가 뜨게됨 */}
            <p>{props.shoes[id].price}</p>
            <Info stock={props.stock}></Info>
            <button className="btn btn-danger" onClick={()=>{
              //props.stockState([3]);
              StockChange(props, id-1);

            }}>주문하기</button> 
            &nbsp;
            <button className="btn btn-danger" 
              onClick={()=>{ history.goBack(); }}>뒤로가기</button> 
          </div>
        </div>
      </div>
  )
}


function Alert(){
  return(
    <div className='my-alert'>
      <p>재고 얼마 없슈</p>
    </div>
  )
}


function Info(props){
  return(
    <p>재고 : {props.stock[0]}</p>
  )
}


function StockChange(props, id){
  var stock = [...props.stock];
  return (
    stock[id] > 0
    ? (
      stock[id]--,
      props.stockState(stock)
    )
    : ''
  )
}


export default Detail;